.program-description {
  background-color: @main-bgr;
  padding: 24px 22px;

  @media (min-width: @tablet-width) {
    padding-top: 70px;
    padding-left: 55px;
    padding-bottom: 55px;

    &--slim {
      padding-right: 275px;
    }

    &--pro {
      padding-right: 310px;
    }
  }

  @media (min-width: @desktop-width) {
    padding: 50px;
    width: 570px;
  }
}

.program-description__title {
  font-size: 24px;
  line-height: 37px;
  font-weight: normal;
  font-family: @main-font;
  color: @prg-dscr-color;
  margin: 0;
  min-width: 175px;
  min-height: 51px;
  padding: 10px 2px 2px 70px;
  position: relative;

  @media (min-width: @tablet-width) {
    font-size: 36px;
    line-height: 37px;
    padding: 1px;
  }

  @media (min-width: @desktop-width) {
    text-align: left;
    padding-left: 165px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    background-color: @green-button;
    border-radius: 50%;

    @media (min-width: @tablet-width) {
      width: 200px;
      height: 200px;
      top: 15px;
      left: 390px;
    }

    @media (min-width: @desktop-width) {
      width: 100px;
      height: 100px;
      top: -25px;
      left: 0;
    }
  }

  &--slim {
    &::after {
      content: "";
      position: absolute;
      width: 50px;
      height: 50px;
      top: 0;
      left: 10px;
      background-image: url("../img/icon-cat-slim.svg");
      background-repeat: no-repeat;

      @media (min-width: @tablet-width) {
        width: 150px;
        height: 200px;
        left: 420px;
        top: 0;
      }

      @media (min-width: @desktop-width) {
        width: 70px;
        height: 100px;
        left: 10px;
        top: -25px;
      }
    }
  }

  &--pro {
    &::after {
      content: "";
      position: absolute;
      width: 67px;
      height: 28px;
      top: 10px;
      left: -10px;
      background-image: url("../img/icon-cat-pro.svg");
      background-repeat: no-repeat;

      @media (min-width: @tablet-width) {
        width: 270px;
        height: 120px;
        left: 355px;
        top: 60px;
      }

      @media (min-width: @desktop-width) {
        width: 135px;
        height: 55px;
        left: -18px;
        top: 0;
      }
    }
  }
}

.program-description__text {
  font-size: 14px;
  line-height: 18px;
  font-family: @sub-font;
  margin-top: 24px;
  margin-bottom: 35px;
  padding: 1px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
    margin-top: 10px;
    margin-bottom: 60px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 65px;
    margin-bottom: 30px;
    font-size: 16px;
    line-height: 24px;
  }
}

.program-description__link {
  text-decoration: none;
  color: @prg-dscr-color;
  font-family: @main-font;
  font-size: 16px;
  line-height: 16px;
  position: relative;
  display: inline-block;
  padding-right: 40px;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 24px;
    padding-right: 50px;
  }

  &::after {
    content: "";
    width: 24px;
    height: 11px;
    position: absolute;
    top: 2px;
    right: 0;
    background-image: url("../img/icon-right-arrow-long.svg");

    @media (min-width: @tablet-width) {
      top: 7px;
    }
  }
}
